.gallery {
    background-image: linear-gradient(var(--secondaryBgColor) 42%, var(--primaryBgColor) 42%, var(--primaryBgColor));
}
.gallery__grid {
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 2px;
    
    @include tablet {
        padding: 0 40px;
        gap: 4px;
    }
    @include desktop {
        padding: 0 20px;
        gap: 7px;
    }
}
.gallery__img {
    border-radius: 13%;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.gallery__item {
    transform: scale(1);
}
@keyframes scales {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
 }

.gallery__item:hover,
.gallery__item:focus {
    animation: scales 1000ms ease 0ms;
    z-index: 2;
}
.gallery__item:first-child {
    grid-column: 1/2;
    grid-row: 4/7;
  }
  
.gallery__item:nth-child(2) {
    grid-column: 2/3;
    grid-row: 3/6;
  }
  
.gallery__item:nth-child(3) {
    grid-column: 2/3;
    grid-row: 6/9;
  }
  
.gallery__item:nth-child(4) {
    grid-column: 3/5;
    grid-row: 1/7;
  }
  
.gallery__item:nth-child(5) {
    grid-column: 3/4;
    grid-row: 7/10;
  }
  
.gallery__item:nth-child(6) {
    grid-column: 5/6;
    grid-row: 3/6;
  }
  
.gallery__item:nth-child(7) {
    grid-column: 5/6;
    grid-row: 6/9;
  }
  
.gallery__item:nth-child(8) {
    grid-column: 6/7;
    grid-row: 2/5;
  }
  
.gallery__item:nth-child(9) {
    grid-column: 6/7;
    grid-row: 5/8;
  }
  
.gallery__item:nth-child(10) {
    grid-column: 7/8;
    grid-row: 4/7;
  }